	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Lantern March","date":"16 Oct 2004","slug":"/2004-10-16-lantern-march/","tags":"Y2004"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2004/10/haybales.jpg\" border=\"0\" height=\"240\" width=\"320\" alt=\"haybales.jpg\" /></p>\n<p>Last night we hosted a <em>Laternenumzug</em> for Christopher's <em>Tagesmutter</em>, even though Christopher has now graduated to Kindergarten and no longer attends day care. We had about 16 parents and 8 children marching in the drizzle through the fields with paper lanterns. We set up hay bales and some Halloween decorations in the carport, and offered pretzels and warm drinks. The kids played in the back yard, pretending it was not dark. Most of Heidi's kids are younger than 3, so most of the guests headed home by 8 pm.  A hard core stayed a little longer.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2004/10/jacques.jpg\" border=\"0\" height=\"180\" width=\"135\" alt=\"jacques.jpg\" /></p>"},"next":{"fields":{"title":"Chilling out the Burger King","date":"16 Oct 2004","slug":"/2004-10-16-chilling-out-the-burger-king/"}},"previous":{"fields":{"title":"Conquerors or Liberators Revisited","date":"17 Oct 2004","slug":"/2004-10-17-conquerors-or-liberators-revisited/"}}},{"node":{"fields":{"title":"Chilling out the Burger King","date":"16 Oct 2004","slug":"/2004-10-16-chilling-out-the-burger-king/","tags":"Y2004"},"html":"<p><a href=\"http://ahawkins.org/archives.html#note_241\" title=\"code: theWebSocket;\">code: theWebSocket: Chilling out the Burger King</a> \"One of my patients had been having it his way down at the local Burger King when the elephant joined him for lunch and made itself at home on his chest...\" I love it when Alwin talks shop.</p>"},"next":{"fields":{"title":"Butt Out!","date":"16 Oct 2004","slug":"/2004-10-16-butt-out/"}},"previous":{"fields":{"title":"Lantern March","date":"16 Oct 2004","slug":"/2004-10-16-lantern-march/"}}},{"node":{"fields":{"title":"Butt Out!","date":"16 Oct 2004","slug":"/2004-10-16-butt-out/","tags":"Y2004"},"html":"<p><a href=\"http://www.jinglelady.us/blog/archives/000749.html\" title=\"Adventures of an American Girl in Germany: What Arrogance\">Adventures of an American Girl in Germany: What Arrogance</a> \"The Guardian, a UK newspaper, has an <a href=\"http://www.guardian.co.uk/uselections2004/story/0,13918,1326033,00.html\" title=\"Guardian Unlimited | US elections 2004 | My fellow non-Americans ...\">article</a> urging Europeans concerned about the US Election to write a personal letter to people living in Clark County, Ohio...\" Like she says, butt out! Or would you like our CIA to influence <em>your</em> election?</p>"},"next":{"fields":{"title":"Morality","date":"15 Oct 2004","slug":"/2004-10-15-morality/"}},"previous":{"fields":{"title":"Chilling out the Burger King","date":"16 Oct 2004","slug":"/2004-10-16-chilling-out-the-burger-king/"}}}],"pathPrefix":"","first":false,"last":false,"index":402,"pageCount":1101}}

